import {
  Streamlit,
  withStreamlitConnection,
  ComponentProps,
} from "streamlit-component-lib"
import React, { useCallback, useEffect, useMemo, useState, ReactElement } from "react"

function MyComponent({ args, disabled, theme }: ComponentProps): ReactElement {
  type EventUserStatus = "Refusé" | "Accepté" | "Attente de réponse" | "Bloqué";
  const { eventName, eventLocation, eventDate, eventCreator, eventUserStatus, eventFirstImage} = args;
  
  const isBlocked = eventUserStatus === "Bloqué";
  const mappingStatusColor = {
    "Refusé": "#FF4B4B",
    "Accepté": "#4CAF50",
    "Attente de réponse": "#FFA500",
    "Bloqué": "#A9A9A9"
};

  const statusColor = mappingStatusColor[eventUserStatus as EventUserStatus];

  const [isClicked, setIsClicked] = useState(false)

  useEffect(() => {
    Streamlit.setComponentValue(isClicked)
  }, [isClicked])

  useEffect(() => {
    Streamlit.setFrameHeight()
  }, [])

  const onClicked = useCallback(() => {
    setIsClicked(true);
  }, []);



  const containerStyle = useMemo(() => {
    return {
        width: '100%',
        maxWidth: '480px',
        height: '120px',
        border: '1px solid #ddd',
        borderRadius: '15px',
        backgroundColor: isBlocked ? '#e0e0e0' : '#f4f4f4',
        color: isBlocked ? '#888' : '#333',
        marginBottom: '10px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        opacity: isBlocked ? 0.6 : 1,
    };
    }, [isBlocked]);

    const imageStyle = useMemo(() => {
        return {
            width: '25%',
            height: '100%',
            aspectRatio: '1 / 1',
            backgroundColor: eventFirstImage ? 'transparent' : (isBlocked ? '#555' : '#000'),
            borderTopLeftRadius: '15px',
            borderBottomLeftRadius: '15px',
            backgroundImage: eventFirstImage ? `url(${eventFirstImage})` : 'none',
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            objectFit: 'cover' as const,
        };
    }, [isBlocked, eventFirstImage]);


    const eventInfosStyle: React.CSSProperties = {
        flexGrow: 1,
        margin: '10px',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
    };

    const eventTitleStyle = useMemo(() => {
        return {
            fontSize: '20px',
            fontWeight: 'bold',
            color: isBlocked ? '#666' : '#333',
        };
    }, [isBlocked]);

    const dividerStyle: React.CSSProperties = {
        width: '100%',
        borderTop: '1px solid #aaa',
        margin: '5px 0',
    };

    const eventFooterInfosStyle : React.CSSProperties = {
        display: 'flex',
        justifyContent: 'space-between',
        width: '100%',
        marginTop: '5px',
        alignItems: 'center',
    };

    const eventSpecificInfosStyle : React.CSSProperties = {
        display: 'flex',
        flexDirection: 'column',
        gap: '2px',
        fontSize: '12px',
        justifyContent: 'space-between',
    };

    const eventStatusStyle = useMemo(() => {
        return {
            backgroundColor: statusColor,
            color: '#fff',
            border: 'none',
            padding: '5px 10px',
            borderRadius: '5px',
            width: '140px',
            height: '30px',
            fontWeight: 'bold',
            textAlign: 'center' as const
        };
    }, [statusColor]);

    const showButtonStyle = useMemo(() => {
        return {
            backgroundColor: isBlocked ? '#ccc' : '#eda8da',
            color: isBlocked ? '#999' : '#fff',
            border: 'none',
            padding: '5px 10px',
            borderRadius: '5px',
            width: '140px',
            height: '30px',
            textAlign: 'center' as const,
            cursor: isBlocked ? 'not-allowed' : 'pointer',
            fontWeight: 'bold',
        };
    }, [isBlocked]);
  
  return (
    <div style={containerStyle}>
        <div style={imageStyle}></div>
        <div style={eventInfosStyle}>
            <div style={eventTitleStyle}>{eventName}</div>
            <div style={dividerStyle}></div>
            <div style={eventFooterInfosStyle}>
                <div style={eventSpecificInfosStyle}>
                    <div>📍 {eventLocation}</div>
                    <div>📅 {eventDate}</div>
                    <div>👤 {eventCreator}</div>
                </div>
                <div style={eventSpecificInfosStyle}>
                    <span style={eventStatusStyle}>{eventUserStatus}</span>
                    <button
                        style={showButtonStyle}
                        onClick={onClicked}
                        disabled={disabled}
                    >
                        Voir Plus
                    </button>
                </div>
            </div>
        </div>
    </div>
  )
}

export default withStreamlitConnection(MyComponent)